html {
  font-size: 100%;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--font-inter);
  background: var(--bg-color);
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  color: var(--color);
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.visually-hidden {
  position: absolute;
  inset-inline-start: -625rem;
  top: auto;
  width: 0.0625rem;
  height: 0.0625rem;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

:root {
  --bg-color: hsl(185, 15%, 96%);
  --card-bg-color: hsl(0, 0%, 100%);
  --card-shadow: hsl(185, 30%, 70%, 0.25);
  --color: hsl(200, 25%, 15%);
  --color-muted: hsl(200, 12%, 40%);
  --primary-accent: hsl(175, 65%, 38%);
  --primary-accent-hover: hsl(175, 65%, 32%);
  --primary-accent-fg: hsl(0, 0%, 100%);
  --primary-accent-shadow: hsl(175, 65%, 38%, 0.35);
  --restart-btn: hsl(25, 85%, 45%);
  --restart-btn-hover: hsl(25, 85%, 38%);
  --restart-btn-fg: hsl(0, 0%, 100%);
  --restart-btn-shadow: hsl(25, 85%, 45%, 0.3);
  --input-bg: hsl(185, 10%, 98%);
  --input-border: hsl(185, 20%, 80%);
  --input-border-focus: hsl(175, 65%, 45%);
  --input-placeholder: hsl(200, 10%, 55%);
  --incorrect: hsl(355, 75%, 45%);
  --incorrect-bg: hsl(355, 80%, 94%);
  --success: hsl(155, 65%, 32%);
  --success-bg: hsl(155, 50%, 92%);
  --hint-high: hsl(20, 85%, 45%);
  --hint-low: hsl(215, 75%, 45%);
  --history-color: hsl(200, 10%, 35%);
  --history-border: hsl(185, 15%, 88%);
  --history-item-bg: hsl(185, 12%, 96%);
  --toggle-bg: hsl(185, 15%, 85%);
  --toggle-knob: hsl(45, 95%, 50%);
  --toggle-icon: hsl(35, 90%, 40%);
}

[data-theme=dark] {
  --bg-color: hsl(220, 25%, 10%);
  --card-bg-color: hsl(220, 22%, 16%);
  --card-shadow: hsl(220, 40%, 5%, 0.5);
  --color: hsl(210, 15%, 92%);
  --color-muted: hsl(210, 10%, 65%);
  --primary-accent: hsl(175, 70%, 48%);
  --primary-accent-hover: hsl(175, 70%, 55%);
  --primary-accent-fg: hsl(220, 25%, 10%);
  --primary-accent-shadow: hsl(175, 70%, 48%, 0.35);
  --restart-btn: hsl(25, 80%, 55%);
  --restart-btn-hover: hsl(25, 80%, 62%);
  --restart-btn-fg: hsl(220, 25%, 10%);
  --restart-btn-shadow: hsl(25, 80%, 55%, 0.3);
  --input-bg: hsl(220, 22%, 12%);
  --input-border: hsl(220, 18%, 28%);
  --input-border-focus: hsl(175, 70%, 48%);
  --input-placeholder: hsl(210, 10%, 50%);
  --incorrect: hsl(355, 70%, 62%);
  --incorrect-bg: hsl(355, 45%, 15%);
  --success: hsl(155, 60%, 55%);
  --success-bg: hsl(155, 35%, 14%);
  --hint-high: hsl(20, 80%, 58%);
  --hint-low: hsl(215, 70%, 60%);
  --history-color: hsl(210, 10%, 68%);
  --history-border: hsl(220, 16%, 24%);
  --history-item-bg: hsl(220, 20%, 13%);
  --toggle-bg: hsl(220, 20%, 20%);
  --toggle-knob: hsl(220, 50%, 70%);
  --toggle-icon: hsl(220, 40%, 85%);
}

@media (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    --bg-color: hsl(220, 25%, 10%);
    --card-bg-color: hsl(220, 22%, 16%);
    --card-shadow: hsl(220, 40%, 5%, 0.5);
    --color: hsl(210, 15%, 92%);
    --color-muted: hsl(210, 10%, 65%);
    --primary-accent: hsl(175, 70%, 48%);
    --primary-accent-hover: hsl(175, 70%, 55%);
    --primary-accent-fg: hsl(220, 25%, 10%);
    --primary-accent-shadow: hsl(175, 70%, 48%, 0.35);
    --restart-btn: hsl(25, 80%, 55%);
    --restart-btn-hover: hsl(25, 80%, 62%);
    --restart-btn-fg: hsl(220, 25%, 10%);
    --restart-btn-shadow: hsl(25, 80%, 55%, 0.3);
    --input-bg: hsl(220, 22%, 12%);
    --input-border: hsl(220, 18%, 28%);
    --input-border-focus: hsl(175, 70%, 48%);
    --input-placeholder: hsl(210, 10%, 50%);
    --incorrect: hsl(355, 70%, 62%);
    --incorrect-bg: hsl(355, 45%, 15%);
    --success: hsl(155, 60%, 55%);
    --success-bg: hsl(155, 35%, 14%);
    --hint-high: hsl(20, 80%, 58%);
    --hint-low: hsl(215, 70%, 60%);
    --history-color: hsl(210, 10%, 68%);
    --history-border: hsl(220, 16%, 24%);
    --history-item-bg: hsl(220, 20%, 13%);
    --toggle-bg: hsl(220, 20%, 20%);
    --toggle-knob: hsl(220, 50%, 70%);
    --toggle-icon: hsl(220, 40%, 85%);
  }
}
:root {
  --font-inter: 'Inter', sans-serif;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h2 {
  font-size: 1.75rem;
}

a {
  text-decoration: none;
}

input,
button {
  border: none;
}

.container {
  max-width: clamp(20rem, 90vw, 75rem);
  padding: clamp(1rem, 4vw, 3rem);
  border-radius: 1rem;
  background: var(--card-bg-color);
  box-shadow: var(--card-shadow);
  align-self: center;
}

:root {
  --theme-icon: url(../../../images/sun.svg);
}

[data-theme=dark] {
  --theme-icon: url(../../../images/moon.svg);
}

@media (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    --theme-icon: url(../../../images/moon.svg);
  }
}
.form {
  margin-bottom: 2.5rem;
}
.form input {
  font-size: 1.25rem;
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 1.25rem;
  padding: 1.125rem 2.25rem;
  background: var(--input-bg);
  color: var(--color);
  border: 0.0625rem solid var(--input-border);
}
.form input::placeholder {
  color: var(--color);
}
.form__button {
  display: flex;
  gap: 1rem;
  justify-content: stretch;
}
.form__button button {
  font-size: 1rem;
  padding: 0.75rem 1.5rem;
  border-radius: 1.5rem;
  font-weight: 600;
  transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out, transform 0.1s ease-in-out;
}
.form__button button:active {
  transform: scale(0.98);
}
.form__button button[type=submit] {
  background: var(--primary-accent);
  box-shadow: var(--primary-accent-shadow);
}
.form__button button[type=submit]:hover {
  background: var(--primary-accent-hover);
}
.form__button button[type=reset] {
  background: var(--restart-btn);
  box-shadow: var(--restart-btn-shadow);
}
.form__button button[type=reset]:hover {
  background: var(--restart-btn-hover);
}

.header {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
@media (max-width: 39.9375em) {
  .header {
    align-items: start;
    flex-direction: column;
    gap: 0.625rem;
  }
}
.header__title {
  font-size: 1.5rem;
  font-weight: 700;
}

.toggle {
  outline: none;
  border: none;
}
.toggle__wrapper {
  position: relative;
  height: 1.875rem;
  width: 3.75rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.toggle__wrapper input[type=radio] {
  opacity: 0;
  z-index: 2;
  width: 50%;
  height: 100%;
  cursor: pointer;
  margin: 0;
  position: relative;
}
input[type=radio][id=light]:checked ~ .toggle__button {
  inset-inline-start: 0.1875rem;
}
input[type=radio][id=dark]:checked ~ .toggle__button {
  inset-inline-start: calc(100% - 1.6875rem);
}
.toggle__wrapper:hover .toggle__background {
  opacity: 0.9;
}
.toggle__button {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  top: 0.1875rem;
  position: absolute;
  inset-inline-start: 0.1875rem;
  z-index: 5;
  background-color: var(--toggle-icon);
  -webkit-mask-image: var(--theme-icon);
  mask-image: var(--theme-icon);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  mask-size: contain;
  -webkit-mask-position: center;
  mask-position: center;
  transition: inset-inline-start 0.3s cubic-bezier(0.4, 0, 0.2, 1), background-color 0.3s ease-in-out;
}
.toggle__background {
  position: absolute;
  border-radius: 0.9375rem;
  height: 100%;
  width: 100%;
  top: 0;
  display: block;
  inset-inline-start: 0;
  pointer-events: none;
  background: var(--toggle-bg);
  transition: background-color 0.3s ease-in-out, opacity 0.2s ease-in-out;
}

.card {
  background: var(--card-bg-color);
}
.card__history__past-guesses, .card__history__correct-games {
  margin-bottom: 0.5rem;
}
.card__history__past-guesses h2, .card__history__correct-games h2 {
  margin-bottom: 1rem;
}
.card__history__past-guesses ul, .card__history__correct-games ul {
  list-style: none;
}
.card__history__past-guesses ul li, .card__history__correct-games ul li {
  margin-bottom: 0.5rem;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  background: var(--history-item-bg);
  font-size: 0.875rem;
  border: 0.0625rem solid var(--history-border);
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
.card__history__past-guesses--incorect, .card__history__correct-games--incorect {
  color: var(--incorrect);
}
.card__history__past-guesses--incorect--high, .card__history__correct-games--incorect--high {
  color: var(--hint-high);
}
.card__history__past-guesses--incorect--low, .card__history__correct-games--incorect--low {
  color: var(--hint-low);
}
.card__history__past-guesses--correct, .card__history__correct-games--correct {
  color: var(--success);
}
.card__history__past-guesses {
  margin-bottom: 2.5rem;
}
.card__history__correct-games--history {
  color: var(--history-color);
}
.card__history__correct-games--history--highlight {
  color: var(--success);
}